import { defineDocumentType, makeSource } from 'contentlayer/source-files';
/** @type {import('contentlayer/source-files').ComputedFields} */
const computedFields = {
  slug: {
    type: 'string',
    resolve: (doc) => doc._raw.flattenedPath,
  },
  structuredData: {
    type: 'object',
    resolve: (doc) => ({
      '@context': 'https://schema.org',
      '@type': 'BlogPosting',
      title: doc.title,
      slug: doc.slug,
      date: doc.date,
      description: doc.gist,
      author: {
        '@type': 'Person',
        name: 'Rithik',
      },
    }),
  },
};

export const Post = defineDocumentType(() => ({
  name: 'Post',
  filePathPattern: `**/*.md`,
  contentType: 'md',
  fields: {
    title: {
      type: 'string',
      required: true,
    },
    date: {
      type: 'string',
      required: true,
    },
    gist: {
      type: 'string',
      required: true,
    }
  },
  computedFields,
}));

export default makeSource({
  contentDirPath: '_posts',
  documentTypes: [Post],
});